/* Contract.css */

#contract {
  text-align: center;
  font-size: 2em;
  margin-left: 20px;
}

img {
  width: 100%;
  height: auto;
  max-width: 1200px;
  max-height: 800px;
  margin: 20px 0;
}

fieldset {
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid #00CED1;
  border-radius: 10px;
  box-sizing: border-box;
  text-align: center;
  font-size: 1.2em;
}
