/* Resume2.css */

#resume {
  margin: 20px auto;
  max-width: 800px;
}

fieldset {
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid #00CED1;
  border-radius: 10px;
  box-sizing: border-box;
  text-align: center;
}

legend {
  font-size: 1.2em;
  font-weight: bold;
  color: #333;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 10px 0;
  font-size: 0.7em;
  color: #555;
}

em {
  display: block;
  margin: 20px 0;
  font-size: 1em;
}
