/* Resume.css */

#resume {
  margin-left: 50px;
  max-width: 800px;
  text-align: center;
}

table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

th {
  background-color: #e3fcff;
}

em {
  display: block;
  margin: 20px 0;
}
