/* portpolio.css */

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100vh;
  margin-top: 50px; /* 수정된 부분: 상단 마진을 추가해 간격을 늘림 */
}

nav {
  text-align: center;
  margin-top: 20px; /* 수정된 부분: 링크 간의 간격을 늘림 */
}

/* Links styling */
a {
  text-decoration: none;
  color: #00CED1;
  font-size: 1.2em;
  margin: 0 10px; /* 수정된 부분: 링크 간의 간격을 늘림 */
}

a:hover {
  text-decoration: underline;
}